<template lang="html">
  <el-col :span="1" class="left-nav">
    <div>
      <ul>
        <li class="icon iconfont icon-goumai">
          <span>收银</span>
        </li>
        <li class="icon iconfont icon-dianpu">
          <span>店铺</span>
        </li>
        <li class="icon iconfont icon-shangpin">
          <span>商品</span>
        </li>
        <li class="icon iconfont icon-huiyuanka">
          <span>会员</span>
        </li>
        <li class="icon iconfont icon-iconfontshujutongji">
          <span>统计</span>
        </li>
        <li class="icon iconfont icon-gongnengjianyi">
          <span>设置</span>
        </li>
      </ul>
    </div>
  </el-col>
</template>

<script>
export default {
}
</script>

<style lang="css">
.left-nav{
  color: #fff;
  font-size: 15px;
  height: 100%;
  background-color: #1d8ce0;
  float: left;
  /*width: 5%;*/
}
.iconfont{
  font-size: 30px;
}
.iconfont span{
  display: block;
  font-size: 15px;
}
.left-nav ul {
  padding: 0;
  margin: 0;
}
.left-nav ul li{
  list-style: none;
  text-align: center;
  padding: 20px 10px;
  border-bottom: 1px solid #fff;
}
.left-nav ul li:hover{
  background-color: #fa4242;
  cursor: pointer;
}

</style>



