<template lang="html">
    <div class="pos">
    <el-row>
      <leftNav></leftNav>
      <el-col :span="7" class="pos-order" id="order-list">
        <el-tabs>
          <el-tab-pane label="点餐"> <!-- label是tabs的标题-->
            <!-- srtrip带斑马线的表格，默认false；border表示是否带竖线默认false，不写就是默认-->
            <el-table stripe border :data="tableData" style="width: 100%">
              <!-- label标题  props数据的key-->
              <el-table-column label="商品名称" prop="goodsName" width="115"></el-table-column>
              <el-table-column label="数量" prop="count" width="70"></el-table-column>
              <el-table-column label="价格" prop="price" width="90"></el-table-column>
              <el-table-column label="操作" fixed="right" width="120">
                <template scope="scope">
                  <el-button type="text" size="small" @click="addOrderList(scope.row)">添加</el-button>
                  <el-button type="text" size="small" @click="delSingleList(scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="total">
              <span><small>数量：</small><i>{{totalCount}}</i></span>
              <span><small>总价：</small><i>{{totalMoney}}</i></span>
            </div>
            <div class="order-btn">
              <el-button type="success" @click="checkout()">结账</el-button>
              <el-button type="warning">挂单</el-button>
              <el-button type="danger" @click="delAllGoods()">全部删除</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label="挂单">挂单</el-tab-pane>
          <el-tab-pane label="外卖">外卖</el-tab-pane>
        </el-tabs>
      </el-col>

      <el-col :span="16">
        <!-- 常用类 -->
        <div class="often-goods">
          <h3 class="title">常用商品</h3>
          <div class="often-goods-list">
            <ul>
              <li v-for="goods in oftenGoods" @click="addOrderList(goods)">
                <span>{{goods.goodsName}}</span>
                <span class="often-price">￥{{goods.price}}</span>
              </li>
            </ul>
          </div>
        </div>

        <!-- 商品类型 -->
        <div class="goods-type">
          <el-tabs>
            <!-- 汉堡类 -->
            <el-tab-pane label="汉堡">
              <div class="goods-detail">
                <ul>
                  <li v-for="item in typeGoods0" @click="addOrderList(item)">
                    <span><img :src="item.goodsImg" alt="item.goodsName"></span>
                    <span>{{item.goodsName}}</span>
                    <span>￥{{item.price}}</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <!-- 零食类 -->
            <el-tab-pane label="零食">
              <div class="goods-detail">
                <ul>
                  <li v-for="item in typeGoods1" @click="addOrderList(item)">
                    <span><img :src="item.goodsImg" alt="item.goodsName"></span>
                    <span>{{item.goodsName}}</span>
                    <span>￥{{item.price}}</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <!-- 饮料类 -->
            <el-tab-pane label="饮料">
              <div class="goods-detail">
                <ul>
                  <li v-for="item in typeGoods2" @click="addOrderList(item)">
                    <span><img :src="item.goodsImg" alt="item.goodsName"></span>
                    <span>{{item.goodsName}}</span>
                    <span>￥{{item.price}}</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <!-- 套餐类 -->
            <el-tab-pane label="套餐">
              <div class="goods-detail">
                <ul>
                  <li v-for="item in typeGoods3" @click="addOrderList(item)">
                    <span><img :src="item.goodsImg" alt="item.goodsName"></span>
                    <span>{{item.goodsName}}</span>
                    <span>￥{{item.price}}</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import leftNav from '@/components/common/leftNav';
import axios from 'axios';

export default {
  components: {
    leftNav
  },
  data() {
    return {
      tableData: [],
      oftenGoods: [ { "goodsId":1, "goodsName":"香辣鸡腿堡", "price":18 }, { "goodsId":2, "goodsName":"田园鸡腿堡", "price":15 }, { "goodsId":3, "goodsName":"和风汉堡", "price":15 }, { "goodsId":4, "goodsName":"大包薯条", "price":18 }, { "goodsId":5, "goodsName":"脆皮炸鸡腿", "price":20 }, { "goodsId":6, "goodsName":"魔法鸡块", "price":20 }, { "goodsId":7, "goodsName":"可乐大杯", "price":10 }, { "goodsId":8, "goodsName":"雪顶咖啡", "price":18 }, { "goodsId":9, "goodsName":"儿童欢乐套餐", "price":25 }, { "goodsId":10, "goodsName":"快乐全家桶", "price":99 } ],
      typeGoods0:  [ { "goodsId": 1, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg", "goodsName": "香辣鸡腿堡", "price": 18 }, { "goodsId": 2, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg", "goodsName": "田园鸡腿堡", "price": 15 }, { "goodsId": 3, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg", "goodsName": "和风汉堡", "price": 15 } ],
      typeGoods1: [ { "goodsId": 4, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg", "goodsName": "大包薯条", "price": 18 }, { "goodsId": 5, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg", "goodsName": "脆皮炸鸡腿", "price": 20 }, { "goodsId": 6, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg", "goodsName": "魔法鸡块", "price": 20 } ],
      typeGoods2: [ { "goodsId": 7, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos001.jpg", "goodsName": "可乐大杯", "price": 10 }, { "goodsId": 8, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos002.jpg", "goodsName": "雪顶咖啡", "price": 18 } ],
      typeGoods3: [ { "goodsId": 9, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos004.jpg", "goodsName": "儿童欢乐套餐", "price": 25 }, { "goodsId": 10, "goodsImg": "http://7xjyw1.com1.z0.glb.clouddn.com/pos003.jpg", "goodsName": "快乐全家桶", "price": 99 } ],
      totalMoney: 0,
      totalCount: 0
    }
  },
  // created() {
    // 拉取常用商品API
    // axios.get('http://jspang.com/DemoApi/oftenGoods.php')
    // .then((response) => {
    //   // console.log(response);
    //   this.oftenGoods = response.data;
    // }).catch((Error) => {
    //   alert('网络错误')
    // }),
    // 拉取商品类型API
  //   axios.get('http://jspang.com/DemoApi/typeGoods.php')
  //   .then(response => {
  //     // console.log(response);
  //     this.typeGoods0 = response.data[0],
  //     this.typeGoods1 = response.data[1],
  //     this.typeGoods2 = response.data[2],
  //     this.typeGoods3 = response.data[3]
  //   }).catch(Error => {
  //     alert('网络错误')
  //   })
  // },

  mounted() {
    var orderHeight = document.body.clientHeight;
    // console.log(orderHeight);
    document.getElementById('order-list').style.height = orderHeight + 'px';
    document.getElementsByClassName('left-nav')[0].style.height = orderHeight + 'px';
  },

  // 添加、删除核心功能
  methods: {
    // 添加功能
    addOrderList(goods) {
      this.totalCount = 0;
      this.totalMoney = 0;
      // 1、遍历tableData里的数据，然后判断数据是否存在
      let isHave = false;  //判断订单列表是否含有该数据
      for (var i = 0; i < this.tableData.length; i++) {
        // 如果数据里的goodsId == 传进来的goods的goodsId
        if (this.tableData[i].goodsId == goods.goodsId) {
          isHave = true
        }
      };
      if (isHave) {
        // 如果存在，就让该数据的数量进行++
        let arr = this.tableData.filter(item => item.goodsId == goods.goodsId);
        arr[0].count++;
        // console.log(arr);
      }else {
        // 如果不存在，就往数据里添加一个完整的数据
        this.tableData.push({goodsId: goods.goodsId, goodsName: goods.goodsName, price: goods.price, count: 1})
      };

      // 总数和总价
      this.getTotal();
    },
    // 总数功能
    getTotal() {
      this.totalCount = 0;
      this.totalMoney = 0;
      if (this.tableData) {
        this.tableData.forEach(ele => {
          this.totalCount += ele.count;
          this.totalMoney = this.totalMoney + (ele.count * ele.price)
        })
      }
    },

    // 删除单个功能
    delSingleList(goods) {  // goods是v-for 循环出来的
      // console.log(goods);
      this.tableData = this.tableData.filter(item => item.goodsId != goods.goodsId)
        // if (goods.count >= 1) {
        //   this.tableData.count = goods.count--
        // }else {
        //   this.tableData = []
        // };
        this.getTotal()
      },
    // 删除所有
    delAllGoods() {
      this.tableData = [];
      this.totalCount = 0;
      this.totalMoney = 0;
    },

    // 模拟结账功能
    // 因为需要把结账的数据传入后台进行处理，在收到后台处理成功的信息就可以调用下面的方法
    // 这里略过了后台的处理 直接使用
    checkout() {
      if (this.totalCount != 0) {
        this.tableData = [];
        this.totalMoney = 0;
        this.totalCount = 0;
        this.$message({
          message: '结账成功',
          type: 'success'
        })
      }else {
        this.$message.error('没有账单')
      };

    }

  }

}
</script>

<style lang="css">
.pos-order{
  background-color: #eee;
  height: 100%;
  /*使用百分比设置高度无效的*/
}
.order-btn{
  margin-top: 15px;
}
.title{
  /*overflow: hidden;*/
  margin: 0;
  border-bottom:1px solid orange;
  background-color: #f9fafc;
  padding: 10px;
  text-align: left;
}
/*ul, li{
  list-style: none;
  margin: 0;
  padding: 0;
}*/
.often-goods-list ul li{
  list-style: none;
  background: #fff;
  float: left;
  border: 1px solid orange;
  border-radius: 10px;
  margin: 5px;
  padding: 10px;
  cursor: pointer;
}
.often-price{
  color: #58B7FF;
}

.goods-type{
  clear: both;
}


.goods-detail ul li{
  list-style: none;
  width: 20%;
  height: 50px;
  float: left;
  border: 1px solid #cfcfcf;
  padding: 5px;
  margin: 5px;
  position: relative;
  cursor: pointer;
}
.goods-detail img{
  width: 50px;
}
.goods-detail span:nth-child(1){
  position: absolute;
  left: 5px;
  top: 5px;
}
.goods-detail span:nth-child(2){
  font-size: 18px;
  position: absolute;
  left: 65px;
  top: 5px;
}
.goods-detail span:nth-child(3){
  position: absolute;
  left: 65px;
  top: 35px;
  font-size: 14px;
  color: #f00;
}

.total{
  padding: 15px;
  border-bottom: 2px solid #999
}
.total span {
  margin: 20px;
}
.total span i {
  color: #f00;
}

</style>



