  // 添加、删除核心功能
  methods: {
    // 添加功能
    addOrderList: function addOrderList(goods) {
      this.totalCount = 0;
      this.totalMoney = 0;
      // 1、遍历tableData里的数据，然后判断数据是否存在
      var isHave = false; //判断订单列表是否含有该数据
      for (var i = 0; i < this.tableData.length; i++) {
        // 如果数据里的goodsId == 传进来的goods的goodsId
        if (this.tableData[i].goodsId == goods.goodsId) {
          isHave = true;
        }
      };
      if (isHave) {
        // 如果存在，就让该数据的数量进行++
        var arr = this.tableData.filter(function (item) {
          return item.goodsId == goods.goodsId;
        });
        arr[0].count++;
        // console.log(arr);
      } else {
        // 如果不存在，就往数据里添加一个完整的数据
        this.tableData.push({ goodsId: goods.goodsId, goodsName: goods.goodsName, price: goods.price, count: 1 });
      };

      // 总数和总价
      this.getTotal();
    },

    // 总数功能
    getTotal: function getTotal() {
      var _this = this;

      this.totalCount = 0;
      this.totalMoney = 0;
      if (this.tableData) {
        this.tableData.forEach(function (ele) {
          _this.totalCount += ele.count;
          _this.totalMoney = _this.totalMoney + ele.count * ele.price;
        });
      }
    },


    // 删除单个功能
    delSingleList: function delSingleList(goods) {
      // goods是v-for 循环出来的
      // console.log(goods);
      this.tableData = this.tableData.filter(function (item) {
        return item.goodsId != goods.goodsId;
      });
      // if (goods.count >= 1) {
      //   this.tableData.count = goods.count--
      // }else {
      //   this.tableData = []
      // };
      this.getTotal();
    },

    // 删除所有
    delAllGoods: function delAllGoods() {
      this.tableData = [];
      this.totalCount = 0;
      this.totalMoney = 0;
    },


    // 模拟结账功能
    // 因为需要把结账的数据传入后台进行处理，在收到后台处理成功的信息就可以调用下面的方法
    // 这里略过了后台的处理 直接使用
    checkout: function checkout() {
      if (this.totalCount != 0) {
        this.tableData = [];
        this.totalMoney = 0;
        this.totalCount = 0;
        this.$message({
          message: '结账成功',
          type: 'success'
        });
      } else {
        this.$message.error('没有账单');
      };
    }
  }

